import CAbstractObject from "./CAbstractObject";
// import * as errors from '../errors';
// import {api} from '../root';
const MLoggingData = {
    title: { default: "[ ...]", type: "string" },
    test_model: { default: undefined, type: "number" },
    tree_title: { default: "[...]", type: "string" },
    tree_id: { default: undefined, type: "number" },
    allowed_start_datetime: { default: null, type: "datetime" },
    allowed_finish_datetime: { default: null, type: "datetime" },
    start_datetime: { default: null, type: "datetime" },
    finish_datetime: { default: null, type: "datetime" },
    duration: { default: 0, type: "number" },
    status: { default: 0, type: "number" },
    result: { default: undefined, type: "number" },
    teacher: { default: 0, type: "number" },
    access_token: { default: undefined, type: "string" },
    questions_count: { default: 0, type: "number" },
    is_free_time: { default: false, type: "boolean" },
    is_instant_result: { default: false, type: "boolean" },
    is_show_solution: { default: false, type: "boolean" },
    is_fixed_order: { default: false, type: "boolean" },
};
Object.defineProperty(MLoggingData, 'name', { value: "LoggingData" });
Object.freeze(MLoggingData);
// export interface IUserTest extends CAbstractObject  {
//   isValid(): boolean;
//   getInput(): any;
//   getApiUrl(): string;
// }
export default class CLoggingData extends CAbstractObject {
    constructor(source = undefined) {
        // console.log('constructor. source: ', source);
        const model = CLoggingData.model;
        const newItem = {};
        for (var key in model) {
            newItem[key] = (source && source[key]) ? source[key] : model[key].default;
        }
        if (source.id) {
            newItem.id = source.id;
        }
        // console.log('constructor. newItem: ', newItem);
        super(newItem);
    }
    ;
    getModel() {
        return CLoggingData.model;
    }
    getTitle() {
        return `LoggingData: id=${this.id} - ${this.allowed_start_datetime}`;
    }
    getApiUrl() {
        return CLoggingData.apiUrl;
    }
}
CLoggingData.model = MLoggingData;
CLoggingData.apiUrl = '/log_data';
CLoggingData.storeNode = 'CLoggingData';
