import feathers from '@feathersjs/feathers';
import rest from '@feathersjs/rest-client';
import axios from 'axios';
const headers = {
    'Access-Control-Allow-Origin': '*',
    'Content-Type': 'application/json',
    'Accept': '*/*',
    'Access-Control-Allow-Methods': 'POST, PATCH'
    // 'Content-Type' : 'multipart/form-data;',
    // 'Accept' : 'application/json',
    // 'Access-Control-Allow-Origin': 'http://localhost:3000',
    // 'Access-Control-Allow-Credentials': 'true'
    // 'Authorization': token
    // Access-Control-Allow-Origin
};
export const config = {
    timeout: 30000,
    withCredentials: true,
    responseType: 'json',
    xsrfCookieName: 'XSRF-TOKEN',
    xsrfHeaderName: 'X-XSRF-TOKEN',
    headers
};
export function addAuthHeader(context) {
    // const api=useApi()
    // console.log('context.params.headers: ', context.params.headers);
    // const authHeader = api.security.getAuthorizationHeader();
    context.params.headers = {
        ...context.params.headers,
        // ...authHeader
    };
}
function compareRoutes(a, b) {
    if (a.code < b.code) {
        return -1;
    }
    if (a.code > b.code) {
        return 1;
    }
    // a must be equal to b
    return 0;
}
export default class CDataBaseAPI {
    constructor(root) {
        this.root = root;
        this.is_logged = false;
        // this.server_url = process.env.REACT_APP_SERVER_URL;
        this.core = feathers();
        const restClient = rest(process.env.REACT_APP_SERVER_URL);
        // this.core.configure(restClient.fetch(window.fetch, config));
        this.core.configure(restClient.axios(axios, config));
        this.vehicles = this.core.service('vehicles');
        this.users = this.core.service('login');
        // this.vehicles.hooks({
        //   before: {
        //     all: [
        //       addAuthHeader
        //     ],},
        // });
    }
    login(username, password) {
        this.auth = { username, password };
        // const fData = new FormData();
        // fData.append('username', username);
        // fData.append('password', password);
        // console.log('this.auth: ', this.auth);
        // this.vehicles.create({auth:this.auth}, {headers:{'Access-Control-Allow-Origin': '*','Content-Type': 'multipart/form-data', 'Accept' : '*/*'}})
        this.vehicles.create({ auth: this.auth, command: 'LOGIN' }, { headers })
            .then((response) => {
            // this.auth = fData;
            this.is_logged = true;
            if (Array.isArray(response.vehicles)) {
                this.root.cw.vehicles.bulkLoad(response.vehicles);
            }
            if (Array.isArray(response.routes)) {
                this.root.data.routes = response.routes;
                this.root.data.routes.sort(compareRoutes);
            }
            this.root.listeners.run('refresh', ['main-table']);
            // console.log('Login response: ', response)
        })
            .catch((error) => {
            this.auth = undefined;
            this.is_logged = false;
            console.log('Login error: ', error);
        });
    }
    save() {
        if (!this.is_logged) {
            console.log('You are not logged!');
            return;
        }
        const vehicles = this.root.cw.vehicles.get().map((item) => { return { id: item.id, route_id: item.route_id }; });
        const data = {
            auth: this.auth,
            command: 'SAVE',
            vehicles
        };
        this.vehicles.create(data, { headers })
            .then((response) => {
            return response;
        })
            .catch((error) => {
            return error;
        });
    }
}
