import React from 'react';
import Typography from '@mui/material/Typography';
import { createTheme } from "@mui/material/styles";
export const theme = createTheme({
    palette: {
        primary: {
            main: '#5D6E97',
            light: '#8c9cc8',
            dark: '#3C5AA8',
            contrastText: '#fff',
        },
        secondary: {
            main: '#4A9DFF',
            light: '#87ceff',
            dark: '#006fcb',
            contrastText: '#fff',
        },
        warning: {
            main: '#FF5151',
            light: '#ff867e',
            dark: '#c50b28',
            contrastText: '#fff',
        },
        info: {
            main: '#444bfb',
            light: '#8678ff',
            dark: '#0021c7',
            contrastText: '#fff',
        },
        success: {
            main: '#5CDC70',
            light: '#75EE88',
            dark: '#17a942',
            contrastText: '#fff',
        },
        error: {
            main: '#FF1111',
            light: '#ff5e40',
            dark: '#c20000',
            contrastText: '#fff',
        }
    },
    typography: {
        fontFamily: [
            'Ubuntu',
            'Roboto',
        ].join(','),
        h1: {
            fontSize: 20,
            lineHeight: 1.15,
            fontWeight: 700,
            fontStyle: 'normal',
            textTransform: 'uppercase',
        },
        h2: {
            fontSize: 22,
            lineHeight: 21,
            fontWeight: 300,
            fontStyle: 'normal',
        },
        h3: {
            fontSize: 20,
            lineHeight: 1.15,
            fontWeight: 400,
            fontStyle: 'normal',
            textTransform: 'uppercase',
        },
        h4: {
            fontSize: 18,
            lineHeight: 21,
            fontWeight: 300,
            fontStyle: 'normal',
        },
        h5: {
            fontSize: 16,
            lineHeight: 18,
            fontWeight: 300,
            fontStyle: 'normal',
        },
    },
    components: {
        MuiButton: {
            variants: [
                {
                    props: { variant: "contained" },
                    style: {
                        fontSize: '14px',
                        lineHeight: '16px',
                        fontFamily: 'Ubuntu',
                        fontStyle: 'normal',
                        fontWeight: 400,
                        borderRadius: '10px',
                        padding: '11px 28px',
                        textTransform: 'none',
                        margin: '8px',
                        flexGrow: 0,
                        borderollapse: 'collapse'
                    }
                }
            ]
            // fontSize: '1rem',
            // position: 'absolute',
            // left: 15.34%;
            // right: 14.29%;
            // top: 28.95%;
            // bottom: 28.95%;
            //
            // font-family: 'Ubuntu';
            // font-style: normal;
            // font-weight: 400;
        },
    },
    mixins: {
        toolbar: {
            "@media (min-width:0px) and (orientation: landscape)": {
                minHeight: Number(process.env.REACT_APP_TOOLBAR_MIN_HEIGHT)
            },
            "@media (min-width:600px)": {
                minHeight: Number(process.env.REACT_APP_TOOLBAR_HEIGHT)
            },
            minHeight: Number(process.env.REACT_APP_TOOLBAR_HEIGHT)
        }
    }
});
export const desktop_style = {
    drawer: { css: {
            color: '#217061',
            backgroundColor: '#f1f7f6',
            background: 'linear-gradient(155deg, #f1f7f6 0%, rgba(209,242,227,1) 80%, rgba(151,199,177,1) 100%)'
        } },
    header: { css: {
            backgroundColor: '#457f6f',
            color: 'white',
            background: 'linear-gradient(345deg, #457f6f 0%, #73bbb1 35%, rgba(27,91,66,1) 100%)',
        } },
    body: { css: {
            backgroundColor: '#f3faf9',
            background: 'linear-gradient(155deg, #e1e7e2 0%, #c9d2cc 100%)' /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        } }
};
export function StyledH3(props) {
    return (React.createElement(Typography, { variant: "h3", noWrap: true, sx: {
            display: 'flex',
            color: "primary.dark",
            opacity: 0.4
        } }, props.children));
}
