import CLoggingData from "./CLoggingData";
import CVehicles from "./CVehicles";
// import * as errors from '../errors';
const appObjects = {
    "LoggingData": CLoggingData,
    "Vehicles": CVehicles,
};
export default class CContainer {
    // work: boolean;
    constructor(baseClass) {
        this.storage = [];
        // CContainer.api = api;
        const classProto = appObjects[baseClass];
        if (!classProto) {
            throw new Error(`Incorrect class name "${baseClass}". Container NOT created.`);
        }
        classProto.container = this;
        // Object.defineProperty(classProto, 'container', {value: this});
        Object.defineProperty(this, '_classProto', { value: classProto });
        // console.log(`Container class of "${baseClass}" created.`);
        // this.work = true;
    }
    create(param) {
        const newInstance = new this._classProto(param);
        this.storage.push(newInstance);
        return newInstance;
    }
    clear() {
        this.storage = [];
    }
    get(id = undefined) {
        if (typeof id === 'number') {
            return this.getItemByID(id);
        }
        return this.storage;
        // console.log(`Storage of container:`);
        // console.log(this.storage);
    }
    getItemByID(id) {
        const result = this.storage.find((item) => item.id == id);
        return result;
    }
    // getTitles(): any[] {
    //   console.log(`Storage of container:`);
    //   console.log(this.storage);
    //   console.log('Object #0: ', this.storage[0]);
    //   console.log('this.storage.length: ',this.storage.length);
    //   const titles = this.storage.map ((item:any) => item.title);
    //   console.log('titles: ',titles);
    //   return titles;
    // }
    pushItem(item) {
        // console.log('Push item in container. Item: ', item);
        const newInstance = new this._classProto(item);
        // console.log('Inserted item: ', newInstance);
        // newInstance.set(item);
        this.storage.push(newInstance);
        return newInstance;
    }
    popItem(item, raiseError = false) {
        const id = typeof item == 'object' ? item.id : item;
        // console.log('popItem. id: ', id);
        const index = this.storage.findIndex((item) => item.id == id);
        if (index == -1) {
            // raiseError && CContainer.api.log.addError(new errors.AppError(117) );
            return undefined;
        }
        const removeItem = this.storage.splice(index, 1);
        // console.log('Containers: ', this.storage);
        return removeItem;
    }
    replaceOrPushItem(item) {
        const index = this.storage.findIndex((i) => i.id == item.id);
        // const containerItem = this.getItemByID(item.id);
        if (index > -1) {
            this.popItem(item);
        }
        return this.pushItem(item);
    }
    bulkLoad(items) {
        // console.log('BulkLoad started... ', items);
        this.clear();
        if (!Array.isArray(items)) {
            return false;
        }
        for (var i = 0; i < items.length; ++i) {
            // console.log('bulkLoad source item: ', items[i]);
            const newInstance = this.pushItem(items[i]);
            // console.log('bulkLoad inserted item: ', newInstance);
        }
    }
}
