export default class CListener {
    constructor(owner) {
        // console.log(`onstructor of CListener`);
        this.listeners = [];
        this.owner = owner;
    }
    add(type, handler, fields = undefined) {
        const i = this.listeners.findIndex((item) => item.handler == handler);
        if (i == -1) {
            this.listeners.push({
                type,
                handler,
                fields
            });
            // console.log(' listener.  : ', this.listeners.length);
        }
        else {
            this.listeners[i].fields = fields;
        }
    }
    remove(handler) {
        this.listeners = this.listeners.filter((item) => item.handler != handler);
        // const i = this.listeners.findIndex( (item:any) => item.handler=handler);
        // if (i!=-1) {
        //   this.listeners.splice(i,1)
        // } else {
        //   console.log('  handler -   ');
        // }
    }
    run(type, fields = undefined) {
        // console.log(`  ${type} listener ${fields ? ": " : ""}`, fields ? fields :'');
        // console.log('Nums of listeners: ', Array.isArray(this.listeners)?this.listeners.length:0);
        for (let listener of this.listeners) {
            if (listener.type == type) {
                if (!listener.fields) {
                    // console.log('  #1');
                    listener.handler(this.owner);
                    continue;
                }
                if (Array.isArray(fields)) {
                    // console.log(`listener.fields: `, listener.fields);
                    for (let field of fields) {
                        // console.log(`   . field=${field}`)
                        if (listener.fields.includes(field)) {
                            // console.log(`  #2. field: ${field}, fields: `, fields);
                            listener.handler(this.owner);
                        }
                    }
                }
            }
        }
    }
}
